import { Component, OnInit } from '@angular/core';
import {MdSnackBar} from '@angular/material';
import { AsyncLocalStorage } from 'angular-async-local-storage';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import {Router} from '@angular/router';

@Component({
  selector: 'app-notes',
  templateUrl: './notes.component.html',
  styleUrls: ['./notes.component.css']
})
export class NotesComponent implements OnInit {

    dbNotes:FirebaseListObservable<any[]>;
    myNotes=[];
    newNote:Notes = new Notes();
  constructor(protected storage: AsyncLocalStorage,public router:Router,public snackBar: MdSnackBar,public db: AngularFireDatabase) { }
x:string;
userKey:string;
spinner;
  ngOnInit() {
    this.spinner = true;
    this.storage.getItem('email').subscribe(data=>{
      // console.log(data);
       this.x=data;
       var query = this.db.database.ref('/users_List').orderByChild("email").equalTo(this.x).once("value", (snapshot)=> {
         var userData = snapshot.val(); 
          // console.log(userData);
           this.userKey = Object.keys(userData)[0];
        //console.log(Object.keys(userData));
        this.dbNotes = this.db.list('users_List/'+Object.keys(userData)+'/notes');
        var y = this.dbNotes.subscribe(data=>{
    
            this.myNotes = data;

            this.spinner = false;
          //console.log(this.myNotes);
//this.temp = [...data];
          //this.contactsTable.loadingIndicator = false;
         // console.log(this.myContacts);
        })
        
        })
     });
  }

  deleteNote(ts){
    //console.log(ts);
    var x = JSON.parse(JSON.stringify(this.myNotes));
    for(var i=0;i<x.length;i++){
      if(x[i].timeStamp == ts){
        x.splice(i,1);
        break;
      }
    }
    var y = this.db.list('users_List/'+this.userKey);
    y.set('notes',x).then(data=>{
      //console.log(data);
      this.snackBar.open('Note Deleted','',{
        duration:1000,
      })
      
    });
  }

myText:string;
myTitle:string;
  addNotes(){
    this.newNote.notesData = this.myText;
    this.newNote.timeStamp = Date.now();
    this.newNote.notesTitle = this.myTitle;
    console.log(this.newNote);
    var y = JSON.parse(JSON.stringify(this.myNotes));
    y.push(this.newNote);
    var x = this.db.list('users_List/'+this.userKey);
    x.update('notes',y).then(data=>{
      //console.log(data);
      this.snackBar.open('Notes Added','',{
        duration:1000,
      })
      
    });
    this.myText = null;
    this.myTitle =null;
  }


}


export class Notes{
  timeStamp:any;
  notesData:string;
  notesTitle:string;
}



