import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFireAuth } from 'angularfire2/auth';
import { AsyncLocalStorage } from 'angular-async-local-storage';
declare var $:any;
@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(protected storage: AsyncLocalStorage,private router: Router,public afAuth: AngularFireAuth) { }
  myDate:any;
  name:string;
  imageUrl:string;
  imageAvailable:boolean;
  ngOnInit(){
    this.utcTime();
    this.storage.getItem('userName').subscribe(data=>{
      this.name = data;
    })
  /*   this.storage.getItem('photoUrl').subscribe(data=>{
    this.imageUrl = data;
    }) */

  }
  utcTime(): void {
        setInterval(()=> {
            this.myDate = new Date();
        }, 1000);
    }
    goToHome(){
      this.router.navigateByUrl('navbar/home');
    }
 startTime() {
    var today = new Date();
    var h = today.getHours();
    var m = today.getMinutes();
    var s = today.getSeconds();
    m = this.checkTime(m);
    s = this.checkTime(s);
    document.getElementById('txt').innerHTML =
    h + ":" + m + ":" + s;
    var t = setTimeout(this.startTime, 500);
}

 checkTime(i) {
    if (i < 10) {i = "0" + i};  // add zero in front of numbers < 10
    return i;
}
logout() {
  this.afAuth.auth.signOut();
/*   this.imageUrl = "";
  this.userName = "";
  this.isLoggedIn= false; */
  this.storage.clear().subscribe(() => {});
 this.router.navigateByUrl("login");
}

}



