import { Component, OnInit,ViewChild } from '@angular/core';
import {MdSnackBar} from '@angular/material';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { UserService } from './../services/user.service';
import { AsyncLocalStorage } from 'angular-async-local-storage';
import {Router} from '@angular/router';


@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css']
})
export class ContactsComponent implements OnInit {

  @ViewChild('contactsTable') contactsTable:any;
  @ViewChild('panel') panel:any;

  groups=['Friends','Family','School','College','Work'];
  newContact:Contact = new Contact();
  myContacts:Array<Contact> = new Array<Contact>();
  contacts: FirebaseListObservable<any[]>;
  userKey:string;
  editing = {};
  constructor(protected storage: AsyncLocalStorage,public router:Router,public snackBar: MdSnackBar,public db: AngularFireDatabase,public _userService:UserService) { 
  }


  updateValue(event, cell, rowIndex) {
    this.editing[rowIndex + '-' + cell] = false;
    this.myContacts[rowIndex][cell] = event.target.value;
   // console.log(this.myContacts);
    this.myContacts = [...this.myContacts];
  }
spinner:boolean;
  ngOnInit() {
    this.spinner = true;
   // this.contactsTable.loadingIndicator = true;
    this.storage.getItem('email').subscribe(data=>{
     // console.log(data);
      this.x=data;
      var query = this.db.database.ref('/users_List').orderByChild("email").equalTo(this.x).once("value", (snapshot)=> {
        var userData = snapshot.val(); 
         // console.log(userData);
          this.userKey = Object.keys(userData)[0];
       //console.log(Object.keys(userData));
       this.contacts = this.db.list('users_List/'+Object.keys(userData)+'/contacts');
       var y = this.contacts.subscribe(data=>{
         this.myContacts = data;
         this.temp = [...data];
         //this.contactsTable.loadingIndicator = false;
         this.spinner= false;
        // console.log(this.myContacts);
       })
       
       })
    });
  }
  x:string;
  onPage(){

  }
addContact(){
 // console.log(this.newContact);
  //this.contacts.push(this.newContact);
//console.log(this._userService.userEmail);
var y = JSON.parse(JSON.stringify(this.myContacts));
y.push(this.newContact);
var x = this.db.list('users_List/'+this.userKey);
x.update('contacts',y).then(data=>{
  //console.log(data);
  this.snackBar.open('Contact Added','',{
    duration:1000,
  })
  
});
this.newContact.fullName = null;
this.newContact.group = null;
this.newContact.nickName = null;
this.newContact.phoneNumber = null;
this.newContact.place = null;
this.panel.close();
  this.grid();
 // this.router.navigateByUrl('navbar/home');
  }
temp=[];
  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.temp.filter(function(d) {
      return (d.fullName.toLowerCase().indexOf(val) && d.group.toLowerCase().indexOf(val) && d.phoneNumber.toLowerCase().indexOf(val) && d.place.toLowerCase().indexOf(val)) !== -1 || !val;
    });

    // update the rows
    this.myContacts = temp;
    // Whenever the filter changes, always go back to the first page
    //this.table.offset = 0;
  }

  grid(){
    setTimeout(()=>{
      this.contactsTable.recalculate();
    
    },400);
    
  }
  deleteContact(index){
    //this.myContacts.splice(index,1);
   // console.log(this.myContacts);
    var y = JSON.parse(JSON.stringify(this.myContacts));
    y.splice(index,1);
    var x=this.db.list('users_List/'+this.userKey);
    x.set('contacts',y).then(data=>{
      this.snackBar.open('Contact Deleted','',{
        duration:1000,
      })
    })
  
  }

  update(){
    //console.log(this.userKey);
 var x=this.db.list('users_List/'+this.userKey);
 x.update('contacts',this.myContacts).then(data=>{
   this.snackBar.open('Contacts Updated','',{
     duration:1000,
   })
 })
    
  }
openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }
}


export class Contact{
  fullName:string;
  nickName:string;
  phoneNumber:string;
  place:string;
  group:string;
}


