import { Component, OnInit } from '@angular/core';
import {MdSnackBar} from '@angular/material';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { UserService } from './../services/user.service';
import { AsyncLocalStorage } from 'angular-async-local-storage';
import {Router} from '@angular/router';
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {
  x:string;
  userKey:string;
  userName:string;
  conversations:FirebaseListObservable<any[]>;
  myConversations=[];
  users=[];
  chatContact:Chat = new Chat();
  chatConversation=[];
  userMsgs:FirebaseListObservable<any[]>;
  constructor(protected storage: AsyncLocalStorage,public router:Router,public snackBar: MdSnackBar,public db: AngularFireDatabase) { 
    this.userNamesList = this.db.list('/listOfUserNames');
  }
  userNamesList:FirebaseListObservable<any[]>;
  ngOnInit() {
    this.userNamesList.subscribe(data=>{
      console.log(data);
      this.users = data;


      
      this.storage.getItem('userName').subscribe(data=>{
        this.userName = data;
        console.log(this.userName);
      })
      var index = this.users.indexOf(this.userName);
     // this.users.splice(index,1);
    })

    console.log(this.users);
    this.storage.getItem('email').subscribe(data=>{
      // console.log(data);
       this.x=data;
       var query = this.db.database.ref('/users_List').orderByChild("email").equalTo(this.x).once("value", (snapshot)=> {
         var userData = snapshot.val(); 
          // console.log(userData);
           this.userKey = Object.keys(userData)[0];
       // console.log(Object.keys(userData));
        this.conversations = this.db.list('users_List/'+Object.keys(userData)+'/conversations');
        /* var y = this.conversations.subscribe(data=>{
          this.myConversations = data;
          
        }) */
        var z = this.db.list('users_List/'+this.userKey+'/conversations/list');
        z.subscribe(data=>{
          this.myConversations = data;
          console.log(this.myConversations);
        })
        })
     });
  }

  addConnection(event){
    console.log(event.target.innerText);
    var name= event.target.innerText;
    this.chatContact.user1 = name;
    this.chatContact.user2 = this.userName;
    this.chatContact.uniqueKey = name+'key'+this.userName;
    this.chatContact.conversation.push('Connection Added');
    
    
    var p = this.db.list('chatMsgs');
    p.push(this.chatContact);
    
    this.conversations.update('list',[this.chatContact.user1]);

    var query = this.db.database.ref('/users_List').orderByChild("userName").equalTo(name).once("value",(snapshot)=>{
      var x = snapshot.val();
      console.log(x);
      var userKey = Object.keys(x)[0];
      var z = this.db.list('users_List/'+userKey+'/conversations');
      z.update('list',[this.chatContact.user2]);
    })
  }
  p:any;
  chatKey:string;
  q=[];
  goToConnection(e){
    var name = e.target.innerText;
    /* var query = this.db.database.ref('users_List/'+this.userKey+'/conversations').orderByChild("user").equalTo(name).once("value",(snapshot)=>{
      var y = snapshot.val();
      this.chatKey = Object.keys(y)[0];
      this.p = y;
      console.log(this.chatKey);
    });
    var x = this.db.list('users_List/'+this.userKey+'/conversations/'+this.chatKey);
    x.subscribe(data=>{
      var z = data;
      console.log(z[0]);
      this.q = z[0];
    }) */
    var key = name+'key'+this.userName;
    console.log(key);
    var x = this.db.database.ref('chatMsgs').orderByChild("uniqueKey").equalTo(key).once("value",(snapshot)=>{
      //console.log(snapshot.val())
      var chatKey = Object.keys(snapshot.val());
     // console.log(chatKey);
      var z = this.db.list('chatMsgs/'+chatKey);
      z.subscribe(data=>{
        console.log(data[0]);
        this.q = data[0];
      })
    })
    
    console.log(this.p);;
  }
}
class Chat{
  user1:string;
  user2:string;
  conversation=[];
  uniqueKey:string;
}

class ChatMsg{
  user:string;
  msg:string;
  time:any;
}


