import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthService} from './services/auth.service';
import { AppComponent } from './app.component';
import { WeatherComponent } from './weather/weather.component';
import { NewsComponent } from './news/news.component';
import { MapsComponent } from './maps/maps.component';
import { TranslateComponent } from './translate/translate.component';
import { HomePageComponent } from './home-page/home-page.component';
import { ForexComponent } from './forex/forex.component';
import { LoginComponent } from './login/login.component';
import { ContactsComponent } from './contacts/contacts.component';
import { NotesComponent } from './notes/notes.component';
import { NavbarComponent } from './navbar/navbar.component';
import { BirthdaysComponent } from './birthdays/birthdays.component';
import { ChatComponent } from './chat/chat.component';
const routes: Routes = [
  { path: '',redirectTo: '/login', pathMatch: 'full'},
  { path: 'login', component:LoginComponent},
  { path: 'navbar',component:NavbarComponent ,canActivate:[AuthService],
children:[
  { path: '',   component: HomePageComponent ,canActivate:[AuthService]},
  { path: 'home',   component: HomePageComponent ,canActivate:[AuthService]},
  { path: 'weather', component: WeatherComponent ,canActivate:[AuthService]},
  { path: 'news', pathMatch: 'full', component: NewsComponent,canActivate:[AuthService] },
  { path: 'maps', pathMatch: 'full',     component: MapsComponent,canActivate:[AuthService] },
  { path: 'translate', pathMatch: 'full', component:TranslateComponent,canActivate:[AuthService]},
  { path: 'forex', pathMatch: 'full', component:ForexComponent,canActivate:[AuthService]},
  { path: 'contacts', pathMatch: 'full', component:ContactsComponent,canActivate:[AuthService]},
  { path: 'notes', pathMatch: 'full', component:NotesComponent,canActivate:[AuthService]},
  { path: 'birthdays', pathMatch: 'full', component:BirthdaysComponent,canActivate:[AuthService]},
  { path: 'chat', pathMatch: 'full', component:ChatComponent,canActivate:[AuthService]}
]},
  
];
 
@NgModule({
  imports: [ RouterModule.forRoot(routes,{useHash:true}) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}


