import { Component, OnInit, ViewChild } from '@angular/core';
import {MdSnackBar} from '@angular/material';
import { AsyncLocalStorage } from 'angular-async-local-storage';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Router } from '@angular/router';
@Component({
  selector: 'app-birthdays',
  templateUrl: './birthdays.component.html',
  styleUrls: ['./birthdays.component.css']
})
export class BirthdaysComponent implements OnInit {

  dbBdays:FirebaseListObservable<any[]>;
  bDays=[];
  spinner;
  newBday:Bday = new Bday();
  months=['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
x:string;
userKey:string;
  constructor(protected storage: AsyncLocalStorage,public router:Router,public snackBar: MdSnackBar,public db: AngularFireDatabase) { }
 @ViewChild('panel') panel:any;
  ngOnInit() {
    this.spinner =true;
    this.storage.getItem('email').subscribe(data=>{
      // ////console.log(data);
       this.x=data;
       var query = this.db.database.ref('/users_List').orderByChild("email").equalTo(this.x).once("value", (snapshot)=> {
         var userData = snapshot.val(); 
          // //console.log(userData);
           this.userKey = Object.keys(userData)[0];
        ////console.log(Object.keys(userData));
        this.dbBdays = this.db.list('users_List/'+Object.keys(userData)+'/bdays');
        var y = this.dbBdays.subscribe(data=>{
    
            this.bDays = data;

          for(var i=0;i<this.bDays.length;i++){
           // //console.log(this.months[new Date(this.bDays[i].timeStamp).getMonth()]);
           var thisYear:number;
           if((new Date(this.bDays[i].timeStamp).getMonth()) >= (new Date().getMonth())){
            thisYear = new Date().getFullYear();
            this.bDays[i].age = (thisYear)-new Date(this.bDays[i].timeStamp).getFullYear();
           }else{
            thisYear = new Date().getFullYear()+1;
            this.bDays[i].age = (thisYear-1)-new Date(this.bDays[i].timeStamp).getFullYear();
           }
           
            this.bDays[i].dayString = this.months[new Date(this.bDays[i].timeStamp).getMonth()] + ' ' +  new Date(this.bDays[i].timeStamp).getDate() + ',' + thisYear ;
            //console.log(this.bDays[i].dayString);
            var countDownDate = new Date(this.bDays[i].dayString).getTime();
            var now = new Date().getTime();
            var distance = countDownDate - now;
            var days = Math.floor(distance / (1000 * 60 * 60 * 24));
            this.bDays[i].daysToGo = days;

            this.bDays[i].age = (thisYear-1)-new Date(this.bDays[i].timeStamp).getFullYear();
          }
          //console.log(this.bDays);
//this.temp = [...data];
          //this.contactsTable.loadingIndicator = false;
         // //console.log(this.myContacts);
        })
        
        })
        this.spinner = false;
     });
  }

  deleteBday(ts){
    ////console.log(ts);
    var x = JSON.parse(JSON.stringify(this.bDays));
    for(var i=0;i<x.length;i++){
      if(x[i].timeStamp == ts){
        x.splice(i,1);
        break;
      }
    }
    var y = this.db.list('users_List/'+this.userKey);
    y.set('bdays',x).then(data=>{
      ////console.log(data);
      this.snackBar.open('Bday Deleted','',{
        duration:1000,
      })
      
    });
  }
bdayName:string;
newDate:any;
chooseDate(e){
//console.log(this.newDate);
}
  addNotes(){
    //this.newBday.age = '25';
    this.newBday.timeStamp = this.newDate;
    this.newBday.name = this.bdayName;
    //console.log(this.newBday);
    var y = JSON.parse(JSON.stringify(this.bDays));
    y.push(this.newBday);
    var x = this.db.list('users_List/'+this.userKey);
    x.update('bdays',y).then(data=>{
      ////console.log(data);
      this.snackBar.open('Bday Added','',{
        duration:1000,
      })
      
    });
    this.newDate = null;
    this.bdayName = null;
    this.panel.close();
  }

}

export class Bday{
  timeStamp:any;
  name:string;
  age:string;
  daysToGo:any;
  dayString:any;
}



